body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Input-form {
  display: -webkit-flex;
  display: flex; }

.Input {
  width: 100%;
  font-size: 1.5rem;
  line-height: 1.5rem;
  padding: 0.75rem 2.5rem 0.75rem 0.75rem;
  outline: none;
  box-sizing: border-box;
  border: 1px solid #666; }

.submit-button {
  border: none;
  background: none;
  margin-left: -2.5rem;
  color: #999;
  width: 2.5rem; }

.spinning {
  -webkit-animation: simple-spin infinite 1s ease-in-out;
          animation: simple-spin infinite 1s ease-in-out; }

@-webkit-keyframes simple-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes simple-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.Repo {
  border: 1px solid;
  padding: 0.5rem;
  display: -webkit-flex;
  display: flex;
  font-size: 0.9rem; }

.compare-list .Repo {
  margin-bottom: 0.5rem;
  padding: 0; }
  .compare-list .Repo:last-child {
    margin-bottom: 0; }

.compare-list .Repo-name {
  margin: 0 10px 0 0;
  width: 10rem;
  max-width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1.75;
          flex-grow: 1.75;
  word-break: break-all;
  -webkit-align-self: center;
          align-self: center;
  padding: 0.5rem; }

.compare-list .Repo-btn-remove {
  background: red;
  margin-left: auto;
  color: white;
  outline: none;
  border: none;
  -webkit-align-self: stretch;
          align-self: stretch;
  padding: 0 20px;
  font-size: 1.5rem; }
  .compare-list .Repo-btn-remove:hover {
    background-color: darkred; }
  .compare-list .Repo-btn-remove:active {
    -webkit-transform: scale(0.9, 0.9);
            transform: scale(0.9, 0.9); }

.compare-list .Repo-stats {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 0.5rem; }

.compare-list .Repo-stat-name {
  display: block;
  margin: 0 0 5px;
  text-transform: capitalize;
  font-weight: bold; }

.compare-list .Repo-stat-value {
  display: block; }

.app-result-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; }
  .app-result-list .Repo {
    margin: 0.5rem;
    cursor: pointer; }
    .app-result-list .Repo:hover {
      text-decoration: underline; }

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; }

.input-container {
  -webkit-flex-basis: 80%;
          flex-basis: 80%;
  margin-bottom: 2rem; }
  @media screen and (min-width: 640px) {
    .input-container {
      -webkit-flex-basis: 60%;
              flex-basis: 60%; } }
  @media screen and (min-width: 1024px) {
    .input-container {
      -webkit-flex-basis: 40%;
              flex-basis: 40%; } }

.offLineMessage {
  background: red;
  color: white;
  margin: 0 0 3px;
  padding: 3px;
  text-align: center; }

